header {
  flex-shrink: 0;
  padding: 18px 0;
  position: sticky;
  z-index: 2;
  transition: top .3s, height .2s, background-color .25s;
  height: 90px;
  top: 0; }
  header.hidden {
    top: -90px; }
  header.open {
    background-color: #252533; }
  header.close {
    background-color: white; }
  header .container {
    grid-template-rows: 1fr;
    grid-template-columns: 152px 4fr 200px;
    grid-gap: 5px;
    align-items: center; }
  header .logo {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
    grid-gap: 18px;
    width: 63px;
    height: 52px;
    text-decoration: none; }
    header .logo img {
      width: 63px;
      height: 52px; }
    header .logo__text {
      font-size: 39px;
      font-weight: bold;
      color: #ffb248;
      margin: 0 auto;
      line-height: 1; }
  header nav {
    text-align: center; }
    header nav .nav_item {
      margin: 0 30px;
      text-decoration: none;
      opacity: 1;
      color: #848484;
      font-size: 18px;
      line-height: 1.44;
      transition: color 0.2s; }
      header nav .nav_item:hover {
        color: #ffb248; }
      header nav .nav_item:first-child {
        margin-left: 0; }
      header nav .nav_item:last-child {
        margin-right: 0; }
    header nav .active {
      color: #ffb248; }
  header .btn {
    border-radius: 7px;
    width: 100%;
    font-size: 18px;
    text-align: center;
    color: white;
    padding: 14px 0 13px;
    border-color: white;
    cursor: pointer;
    outline: none;
    font-weight: 600;
    position: relative;
    z-index: 1;
    background: linear-gradient(243deg, #ffb248, #ffe22a, #ffe22a, #ffb248);
    font-size: 17px;
    border: none;
    padding: 15px 0 16px;
    background-size: 300% 300%;
    transition: 1s;
    font-weight: 900; }
    header .btn:focus {
      outline: none; }
    header .btn:hover {
      background-position: 100% 50%;
      transform: scale(1.1); }
  header .hamburger {
    display: none; }
  header .hamburger .hamburger-inner,
  header .hamburger .hamburger-inner::before,
  header .hamburger .hamburger-inner::after {
    background-color: #ffb248; }
  header.open .hamburger.is-active .hamburger-inner,
  header.open .hamburger.is-active .hamburger-inner::before,
  header.open .hamburger.is-active .hamburger-inner::after {
    background-color: white; }
  header .nav-wrapper {
    position: relative; }
    header .nav-wrapper .top-block {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2; }

.collapse-template {
  width: 100%; }
  .collapse-template__content {
    padding: 70px 40px 60px;
    display: grid; }
    .collapse-template__content a {
      text-decoration: none;
      line-height: 1.3;
      font-size: 20px;
      color: #ebebeb;
      margin-bottom: 25px; }
      .collapse-template__content a.active {
        color: #ffb248; }
    .collapse-template__content .button-wrapper {
      margin: 45px 0 110px;
      max-width: 200px; }
    .collapse-template__content__info {
      margin-bottom: 75px; }
      .collapse-template__content__info > div {
        margin-bottom: 30px; }
      .collapse-template__content__info--title, .collapse-template__content__info--text {
        font-size: 15px;
        line-height: 1.67;
        margin: 0;
        text-decoration: none; }
      .collapse-template__content__info--title {
        color: #848484;
        font-weight: normal; }
      .collapse-template__content__info--text {
        font-weight: 600;
        color: white; }
    .collapse-template__content__socials {
      display: grid;
      grid-template-columns: repeat(2, minmax(auto, 140px));
      grid-column-gap: 15px;
      grid-row-gap: 20px;
      margin-bottom: 75px; }
      .collapse-template__content__socials a {
        text-decoration: none;
        font-size: 15px;
        font-weight: 600;
        line-height: 1;
        color: white;
        margin: 0; }
    .collapse-template__content__footer p {
      margin: 0;
      line-height: 1.54;
      font-size: 13px;
      color: #848484;
      font-weight: normal; }
    .collapse-template__content__footer p:first-child {
      margin-bottom: 5px; }

.collapse {
  display: block;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.8s cubic-bezier(0, 1, 0, 1);
  position: absolute;
  z-index: 2;
  background: #363646;
  color: #ebebeb;
  top: 44px; }
  .collapse.show {
    width: 100%;
    max-height: 100vh;
    height: calc(100vh - 44px);
    transition: max-height .4s ease-in-out;
    overflow-y: auto; }
    .collapse.show .bubble-wrapper .bubble {
      background-color: rgba(255, 255, 255, 0.03); }

@media (max-width: 1199.98px) {
  header nav .nav_item, header .btn {
    font-size: 16px; } }

@media (max-width: 991.98px) {
  header .container {
    grid-template-columns: 152px 4fr 160px; }
  header nav .nav_item {
    font-size: 14px;
    margin: 0 10px; }
  header .btn {
    font-size: 14px; } }

@media (max-width: 767.98px) {
  header {
    padding: 10px 0;
    height: 45px; }
    header.hidden {
      top: -45px; }
    header .hamburger {
      display: block; }
    header .container {
      grid-template-columns: 69px 24px;
      justify-content: space-between; }
      header .container .button-wrapper, header .container nav {
        display: none; }
    header .logo {
      width: auto;
      height: auto;
      align-items: center;
      grid-gap: 10px; }
      header .logo img {
        width: 28px;
        height: 23px; }
      header .logo p {
        font-size: 18px;
        line-height: 1; }
    header.open .logo p {
      color: white; } }
