footer {
  flex-shrink: 0;
  background-color: #363646;
  border-top: 1px solid #848484; }
  footer .content {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    padding: 46px 0; }
  footer .nav-wrapper {
    display: grid;
    grid-template-columns: 256px auto;
    align-items: center; }
    footer .nav-wrapper img {
      max-width: 63px; }
  footer .nav-footer {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 25px 60px; }
  footer .nav-footer a.nav_item {
    color: #d0d0d0;
    font-size: 18px;
    text-decoration: none;
    line-height: 1.11;
    font-weight: normal;
    transition: color .2s; }
    footer .nav-footer a.nav_item:hover, footer .nav-footer a.nav_item.active {
      color: #ffb248; }
  footer .copyright {
    border-left: solid 1.5px #848484;
    padding: 23px; }
    footer .copyright p {
      font-size: 18px;
      font-weight: normal; }
      footer .copyright p:first-child {
        color: white;
        line-height: 1.11;
        margin: 0 0 15px; }
      footer .copyright p:last-child {
        color: #848484;
        line-height: 1.11;
        margin: 0; }

@media (max-width: 991.98px) {
  footer .nav-wrapper {
    grid-template-columns: 140px auto; } }

@media (max-width: 767.98px) {
  footer {
    display: none; } }
